.header {
  position: relative;
}

.navigation {
  display: grid;
  grid-template-columns: 100px 1fr;
  padding: 35px 0;
}
@media (max-width: 1023px) {
  .navigation {
    grid-template-columns: 52px 1fr;
    padding: 22px 0;
  }
}
@media (max-width: 767px) {
  .navigation {
    padding: 20px 0;
    transition: all 0.6s ease;
  }
}

.navigation__logo {
  display: block;
  margin-right: 30px;
}
@media (max-width: 1023px) {
  .navigation__logo {
    margin-right: 25px;
    margin-top: 8px;
  }
}
@media (max-width: 767px) {
  .navigation__logo {
    margin: 0;
  }
}

.navigation__icon {
  display: block;
  width: 70px;
  height: 38px;
  fill: #f9fbfd;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .navigation__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .navigation__icon {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
  .is-open .navigation__icon {
    fill: #011c40;
  }
}

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  gap: 34px;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 1023px) {
  .navigation__list {
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .navigation__list {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
}

.navigation__item {
  list-style-type: none;
}

.navigation__link {
  display: block;
  padding: 10px 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
}
@media (hover: hover) {
  .navigation__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.navigation__link.focus-visible:focus {
  opacity: 0.6;
}
.navigation__link:active {
  opacity: 0.3;
}
@media (max-width: 1023px) {
  .navigation__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .navigation__link {
    padding: 10px;
    padding-bottom: 8px;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .navigation.is-open {
    display: block;
    width: 89%;
    min-height: 568px;
    padding: 20px 15px;
    background-color: #f9fbfd;
    position: absolute;
    top: 0;
    right: 0;
    transition: all 0.6s ease;
  }
}

@media (max-width: 767px) {
  .nojs .navigation {
    position: static;
    width: 100%;
  }
}

@media (max-width: 767px) {
  .navigation-wrapper {
    display: none;
    width: 100%;
    margin-top: 33px;
  }
}

.is-open .navigation-wrapper {
  display: block;
}

.toggle {
  display: none;
  position: relative;
  justify-self: flex-end;
  width: 24px;
  height: 14px;
  margin-top: 4px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (hover: hover) {
  .toggle:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.toggle.focus-visible:focus {
  opacity: 0.6;
}
.toggle:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .toggle {
    display: block;
  }
}

.toggle__icon {
  display: block;
}
.toggle__icon--basic {
  width: 24px;
  height: 14px;
  stroke: #f9fbfd;
  stroke-width: 2;
}
.toggle__icon--active {
  display: none;
  width: 15px;
  height: 15px;
  stroke: #011c40;
  stroke-width: 2;
}

.is-active .toggle__icon--basic {
  display: none;
}

.is-active .toggle__icon--active {
  display: block;
}

.is-open .toggle {
  position: absolute;
  top: 20px;
  right: 5px;
}

.nojs .toggle {
  display: none;
}

