.cards {
  display: block;
  position: relative;
  cursor: pointer;
}
@media (hover: hover) {
  .cards:hover:not(.focus-visible) figure {
    transform: perspective(600px) rotateY(-180deg);
  }
  .cards:hover:not(.focus-visible) div {
    transform: perspective(600px) rotateY(0);
  }
}
.cards.focus-visible:focus figure {
  transform: perspective(600px) rotateY(-180deg);
}
.cards.focus-visible:focus div {
  transform: perspective(600px) rotateY(0);
}
.cards.focus-visible:active figure {
  transform: perspective(600px) rotateY(-180deg);
}
.cards.focus-visible:active div {
  transform: perspective(600px) rotateY(0);
}

