.social {
  align-self: baseline;
}

.social__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  gap: 28px;
}
@media (max-width: 1023px) {
  .social__list {
    gap: 20px;
  }
}

.social__link {
  display: block;
  padding: 10px 0;
}
@media (hover: hover) {
  .social__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
  .social__link:hover:not(.focus-visible) .social__icon {
    transform: scale(1.3);
    transition: all 0.6s ease;
  }
}
.social__link.focus-visible:focus {
  opacity: 0.6;
}
.social__link.focus-visible:focus .social__icon {
  transform: scale(1.3);
  transition: all 0.6s ease;
}
.social__link:active {
  opacity: 0.3;
}

.social__icon {
  fill: #f9fbfd;
  color: #f9fbfd;
}
.social__icon--ok {
  width: 12px;
  height: 20px;
}
.social__icon--dzen {
  width: 20px;
  height: 20px;
}
.social__icon--reddit {
  width: 20px;
  height: 20px;
}
@media (max-width: 1023px) {
  .social__icon--ok {
    width: 10px;
    height: 16px;
  }
  .social__icon--dzen {
    width: 17px;
    height: 16px;
  }
  .social__icon--reddit {
    width: 17px;
    height: 16px;
  }
}

